<template>
  <div class="columns">
    <div class="column is-full">
      <el-card
          class="box-card"
          shadow="never"
      >
        <div>
          <el-form
              ref="ruleForm"
              :model="ruleForm"
              :rules="rules"
              class="demo-ruleForm"
          >
            <el-form-item prop="title">
              <el-input ref="title"
                        v-model="ruleForm.title"
                        placeholder="输入主题名称"
              />
            </el-form-item>

            <el-form-item prop="categoryId">
              <el-select v-model="ruleForm.categoryId" placeholder="请选择分类">
                <el-option
                    v-for="item in options"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item prop="content">
              <!--Markdown-->
              <div id="vditor"/>
            </el-form-item>

            <b-taginput
                ref="tagInput"
                v-model="ruleForm.tags"
                class="my-3"
                maxlength="5"
                maxtags="5"
                ellipsis
                type="is-info"
                placeholder="请输入主题标签，限制为 5 个字符和 5 个标签"
            />
            <div class="cover-upload">
              <div class="cover_upload_selectType">
                <el-radio-group v-model="coverType">
                  <el-radio-button label="默认"></el-radio-button>
                  <el-radio-button label="外链"></el-radio-button>
                </el-radio-group>
              </div>
              <div v-if="coverType==='默认'" class="cover_up">
                <el-upload
                    drag
                    action="#"
                    :http-request="coverUpload"
                    :file-list="fileList"
                    :disabled="ruleForm.cover!==''"
                >
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                </el-upload>
                <div class="cover_up_preview">
                  <el-image style="width: 100%;height: 180px" :src="ruleForm.cover">
                  </el-image>
                </div>
              </div>
              <div v-else>
                <el-input placeholder="请输入内容" v-model="ruleForm.cover">
                </el-input>
                <el-image style="width: 360px;height: 180px" class="cover_in" :src="ruleForm.cover">
                </el-image>
              </div>
            </div>
            <div class="type">
              <el-radio v-model="ruleForm.type" label="1">原创</el-radio>
              <el-radio v-model="ruleForm.type" label="0">转载</el-radio>
              <el-input placeholder="请输入内容" ref="type" v-show="showUrl"
                        v-model="ruleForm.articleUrl" style="margin-top: 2px">
              </el-input>
            </div>
            <el-form-item class="post_button">
              <el-button
                  type="primary"
                  @click="submitForm('ruleForm',false)"
              >发布
              </el-button>
              <el-button
                  type="success"
                  @click="submitForm('ruleForm',true)"
              >草稿
              </el-button>
              <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>

          </el-form>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import {getUpdateArticleApi,updateArticleApi} from "../../../api/article";
import {mixin} from './mixin'

export default {
  name: 'Update',
  data() {
    return {}
  },
  mixins: [mixin],
  props: {
    articleId: {
      required: true,
      type: Number
    }
  },
  created() {
    this.getCategory()
  },
  mounted() {
    this.$nextTick(()=>{
      this.getUpdateArticle()
    })
  },
  methods: {
    getUpdateArticle() {
      console.log(this.articleId)
      getUpdateArticleApi(this.articleId).then(res => {
        console.log(res)
        this.ruleForm = res.data
        this.renderMarkdown(res.data.content);
      })
    },
    handle(value){
      console.log(value)
      updateArticleApi(value).then(res=>{
        if (res.code===200){
          this.$message({
            type:"success",
            message:"发布成功!"
          })
          this.resetForm('ruleForm')
        }
      })
    }
  },
}
</script>

<style>
.type {
  padding: 10px;
}

.cover_upload_selectType {
  margin: 10px 0;
}

.cover_up {
  display: flex
}

.cover_up_preview {
  width: 360px;
  height: 180px;
  margin-left: 10px
}

.cover_in {
  width: 360px;
  height: 180px;
  margin-top: 10px
}

.post_button {
  display: flex;
  justify-content: flex-end;
}
</style>
