<template>
  <div class="app-container">
    <el-table
        ref="filterTable"
        :data="tableData"
        v-loading="listLoading"
        border
        style="width: 100%">
      <!--编号-->
      <el-table-column
          prop="articleId"
          label="编号"
          sortable
          width="100px"
      >
      </el-table-column>
      <!--标题-->
      <el-table-column
          prop="title"
          label="标题" :show-overflow-tooltip="true">
      </el-table-column>
      <!--封面-->
      <el-table-column
          prop="cover"
          label="封面">
        <template slot-scope="{row}">
          <el-image :src="row.cover" :preview-src-list="[row.cover]"></el-image>
        </template>
      </el-table-column>
      <!--分类-->
      <el-table-column
          prop="category"
          label="分类">
      </el-table-column>
      <!--标签-->
      <el-table-column
          prop="tag"
          label="标签">
        <template slot-scope="{row}">
          <div style="display: flex;justify-content: flex-start;">
            <el-tag
                style="margin-left: 5px"
                type="primary"
                size="mini"
                disable-transitions v-for="(item,index) in row.tags" :key="index">
              {{ item }}
            </el-tag>
          </div>
        </template>
      </el-table-column>
      <!--草稿-->
      <el-table-column
          prop="draft"
          label="草稿"
          width="100"
          :filters="[{ text: '草稿', value: true }, { text: '已发布', value: false }]"
          :filter-method="filterTag"
          filter-placement="bottom-end">
        <template slot-scope="{row}">
          <el-tooltip class="item" effect="dark" :content="row.draft ? '点击发布' : '点击撤销'" placement="top">
            <el-tag style="cursor: pointer"
                @click="switchControl(!row.draft,row,'draft')"
                :type="row.draft ? 'danger' : 'success'"
                disable-transitions>{{ row.draft ? '草稿' : '正文' }}
            </el-tag>
          </el-tooltip>

        </template>
      </el-table-column>
      <!--置顶-->
      <el-table-column
          prop="top"
          label="置顶">
        <template slot-scope="scope">
          <el-tooltip :content="scope.row.top?'置顶':'不置顶'" placement="top">
            <el-switch ref="switch"
                       v-model="scope.row.top"
                       @change="switchControl($event,scope.row,'top')">
            </el-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <!--精贴-->
      <el-table-column
          prop="essence"
          label="精贴">
        <template slot-scope="{row}">
          <el-tooltip :content="row['essence']?'精贴':'非精贴'" placement="top">
            <el-switch
                v-model="row['essence']"
                @change="switchControl($event,row,'essence')">
            </el-switch>
          </el-tooltip>
        </template>
      </el-table-column>
      <!--发布时间-->
      <el-table-column
          prop="createTime"
          label="发布时间"
          width="150px">
        <template slot-scope="{row}">
          {{ dayjs(row["createTime"]).format('YYYY-MM-DD HH:mm:ss') }}
        </template>
      </el-table-column>
      <!--更新时间-->
      <el-table-column
          prop="modifyTime"
          label="更新时间"
          width="150px">
        <template slot-scope="{row}">
          <p v-if="row.modifyTime==null">
            暂未更新
          </p>
          <p v-else>{{ dayjs(row["modifyTime"]).format('YYYY-MM-DD HH:mm:ss') }}</p>
        </template>
      </el-table-column>
      <!--操作-->
      <el-table-column
          label="操作"
          width="180">
        <template slot-scope="scope">
          <el-button type="success" size="mini" icon="el-icon-postcard"
                     @click.native.prevent="viewTopic(scope.row)"></el-button>
          <el-button type="primary" size="mini" icon="el-icon-edit"
                     @click.native.prevent="updateTopic(scope.row)"></el-button>
          <el-button type="danger" size="mini" icon="el-icon-delete"
                     @click.native.prevent="deleteRow(scope.$index, tableData,scope.row)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination v-show="total>0" :total="total" :page.sync="listQuery.pageNo" :limit.sync="listQuery.size"
                @pagination="getList"/>
    <el-dialog title="文章修改" width="80%" :visible.sync="dialogUpdateVisible">
      <Update v-if="dialogUpdateVisible" :article-id="articleId"></Update>
    </el-dialog>
    <el-dialog title="详情" :visible.sync="dialogTableVisible">
      <TopicDetail  :id="articleId" v-if="dialogTableVisible"></TopicDetail>
    </el-dialog>
  </div>
</template>

<script>
import {fetchList,updateStatus,deleteTopicApi} from '@/api/article'
import TopicDetail from "@/components/admin/Detail";
import Pagination from "@/components/admin/Pagination";
import Update from "./Update";
export default {
  name: "List",
  components: {Update, TopicDetail, Pagination},
  data() {
    return {
      listLoading: true,
      total: 0,
      listQuery: {
        pageNo: 1,
        size: 20,
      },
      dialogTableVisible: false,
      dialogUpdateVisible: false,
      tableData: [],
      articleId: 0,
    }
  },
  created() {
    this.getList()
  },
  mounted() {
    // this.$refs.switch.focus()
  },
  methods: {
    getList() {
      this.listLoading = true
      fetchList(this.listQuery).then(res => {
        console.log(res)
        let {data} = res
        this.total = data.total
        this.tableData = data.records
        setTimeout(() => {
          this.listLoading = false
        }, 1.5 * 1000)
      })
    },
    viewTopic(row) {
      this.articleId = row.articleId
      this.dialogTableVisible = true
    },
    updateTopic(row) {
      this.articleId = row.articleId
      this.dialogUpdateVisible = true
    },
    switchControl(status, row, type) {
      this.$confirm('是否继续操作?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        console.log({type,status,articleId: row.articleId})
        if (type==="draft"){
          row.draft=status
        }
        await updateStatus({type,status,articleId: row.articleId})
        this.$message({
          type: 'success',
          message: '操作成功!'
        });
      }).catch(() => {
        row[type] = !status
      });
    },
    deleteRow(index, rows,row) {
      this.$confirm('确定要删除吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteTopicApi(row.articleId).then(()=>{
          rows.splice(index, 1);
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        })
      })

    },
    filterTag(value, row) {
      console.log("value", value)
      console.log("row", row.draft)
      console.log(row.draft === value)
      return row.draft === value;
    },
    filterHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    }
  }
}
</script>

<style scoped>

</style>
