<template>
  <div id="preview"></div>
</template>

<script>

import Vditor from 'vditor'
import 'vditor/dist/index.css'
import { getViewApi} from "../../api/article";

export default {
  name: 'TopicDetail',
  data() {
    return {
      flag: false,
    }
  },
  props:{
    id:{
      type:Number,
      required:true
    }
  },
  mounted() {
    this.fetchTopic()
  },
  methods: {
    renderMarkdown(md) {
      Vditor.preview(document.getElementById('preview'), md, {
        hljs: {style: 'github'}
      })
    },
    // 初始化
    async fetchTopic() {
      getViewApi(this.id).then(response => {
        console.log(response)
        const {data} = response
        this.renderMarkdown(data.topic)
        this.flag = true
      })
    },
  }
}
</script>

<style>
@import '//at.alicdn.com/t/font_2847596_p5p3df017r.css';

#preview {
  min-height: 300px;
}
.handle{
  text-align: center;
}
.iconfont {
  cursor: pointer;
  font-size: 25px !important;
  margin:0 20px;
  user-select: none;
}

.active{
  color: red;
}
</style>
