import {getCategoryApi} from "@/api/article";
import Vditor from "vditor";
import "vditor/dist/index.css"
export const mixin = {
    data() {
        return {
            ruleForm: {
                title: '', // 标题
                tags: [], // 标签
                content: '', // 内容
                categoryId: undefined,
                type: "1",
                cover: "",
                articleUrl: ""
            },
            fileList: [],
            coverType: '默认',
            contentEditor: {},
            options: [],
            rules: {
                title: [
                    {required: true, message: '请输入话题名称', trigger: 'blur'},
                    {min: 1, max: 25, message: '长度在 1 到 25 个字符', trigger: 'blur'}
                ],
                categoryId: [
                    {required: true, message: '请选择分类', trigger: 'change'}
                ],
            },
            showUrl: false,
        }
    },
    computed: {
        getRadio() {
            return this.ruleForm.type
        }
    },
    watch: {
        getRadio(value) {
            this.showUrl = value === "0"
        }
    },
    methods: {
        renderMarkdown(md) {
            this.contentEditor = new Vditor('vditor', {
                height: 500,
                placeholder: '此处为话题内容……',
                theme: 'classic',
                value:md,
                counter: {
                    enable: true,
                    type: 'markdown'
                },
                preview: {
                    delay: 0,
                    hljs: {
                        style: 'github',
                        lineNumber: false
                    }
                },
                tab: '\t',
                typewriterMode: true,
                toolbarConfig: {
                    pin: true
                },
                cache: {
                    enable: false
                },
                mode: 'ir',
                upload: {
                    // 上传字段名称
                    fieldName: 'my-file',
                    // 上传 url
                    url: '',
                    accept: 'image/*,.mp3, .wav, .rar'
                }
            })
        },
        getCategory() {
            getCategoryApi().then(res => {
                // console.log(res)
                this.options = res.data
            })
        },
        coverUpload() {
            this.ruleForm.cover = 'https://desk-fd.zol-img.com.cn/t_s960x600c5/g5/M00/0E/0F/ChMkJ1sYyj6IRDGPAAtL3nBO1FYAAo2TQBTVy8AC0v2266.jpg'
        },

        submitForm(formName, draft) {
            this.ruleForm["draft"] = draft;
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if (
                        this.contentEditor.getValue().length === 1 ||
                        this.contentEditor.getValue() == null ||
                        this.contentEditor.getValue() === ''
                    ) {
                        this.$message({
                            type: "warning",
                            message: '话题内容不可为空'
                        })
                        this.contentEditor.focus()
                        return false
                    }
                    this.ruleForm.content = this.contentEditor.getValue()
                    if (this.showUrl && this.ruleForm.articleUrl === "") {
                        this.$message({
                            type: "warning",
                            message: '转载请注明出处'
                        })
                        this.$refs.type.focus();
                        return
                    }
                    this.handle(this.ruleForm)
                } else {
                    console.log('error submit!!')
                    this.$refs.title.focus()
                    return false
                }
            })
        },
        resetForm(formName) {
            console.log(formName)
            this.$refs[formName].resetFields()
            this.contentEditor.setValue('')
            this.ruleForm.tags = []
            this.ruleForm.cover = ''
        }
    },

}
